body {
    margin: 0;
    padding: 0;
    background-color: #141414;
    color: white;
    width: 100%;
    height: 100%;
}

header {
    display: flex;
    justify-content: space-between;
    position: sticky;
    top: 0;
    background-color: #141414;
    padding-bottom: 10px;
    z-index: 999;
}

.header-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.header-logo > div {
    padding-left: 10px;
}

.header-buttons, .header-buttons > div{
    display: flex;
    align-items: center;
    padding: 10px;
    
}

.header-buttons > div:hover {
    background-color: white;
    color: blue;
    font-weight: bold;
}

.header-buttons > div:first-child {
    padding-right: 20px;
}

.section-title {
    padding: 40px 0 50px 0;
    font-size: x-large;
}

.section-title > span::after {
    content: "5";
    font-weight: bolder;
}

.top {
    display: flex;
    justify-content: space-between;
    padding: 0 40px 10px 0;

}

.top-item > img {
    width: 100%;
}


.first {
    display: flex;
    gap: 2px;
    padding: 10px 40px 10px 0;
}

.top-item >img:hover, .item > img:hover {
    transform: scale(1.2);
}

.second {
    display: flex;
    padding: 10px 40px 30px 0;
    gap: 2px;
}

$i: 1;
.item {
    display: flex;
    @while $i < 13 {
        #img-#{$i} {
           width: 100%;
        }
        $i: $i + 1;
    } 
}

@media screen and (max-width: 1280px) {

    header {
        display: flex;
        justify-content: space-around;
    }

    .header-logo > div {
        display: none;
    }

    .section-title {
        text-align: center;
        font-size: xx-large;
    }

    .section-title > span::after {
        content: "3";
        font-weight: bolder;
    }

    .top {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 10px;
        
    }

    #imgTop1, #imgTop2 {
        display: none;
    }

    .top-item > img {
        width: 225px;
    }

    .first, .second {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .item > img {
        width: 250px;
    }

    .top-item >img:hover, .item > img:hover {
        transform: scale(1.1);
    
    }
}

@media screen and (max-width: 376px) {

    header {
        display: flex;
        justify-content: space-around;
    }

    .header-logo > div {
        display: none;
    }

    #imgTop1, #imgTop2 {
        display: none;
    }

    .section-title {
        text-align: center;
        font-size: x-large;
    }

    .section-title > span::after {
        content: "3";
        font-weight: bolder;
    }

    .top {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .top-item > img {
        min-width: 225px;
    }

    .first, .second {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 10px;
    }

    .top-item > img {
        width: 225px;
    }

    .top-item >img:hover, .item > img:hover {
        transform: scale(1.1);
    
    }

    .item {
        width: 225px;
    }
}